export {}

interface Person1 {
  handsome: string
  address: {
    n: string
  }
}

interface Person2 {
  high: string
  address: {
    n: number
  }
}

type Person = Person1 & Person2

function mixin<T, U>(a: T, b: U) {
  return { ...a, ...b }
}
let r = mixin({ a: 1, b: 2 }, { c: 3, b: '2' })

type Compute<T> = {
  [P in keyof T]: T[P]
}

type x = Compute<typeof r>
